<template>
    <div class="popup" v-if="selected">
      <div class="content">
        <!--Close button-->
        <button 
          class="close" 
          @click="closeWindow">
          <Fa fa=""/> <a >close</a>
        </button>
        <!--Carousel settings-->
        <template>
          <div class="container-fluid">
            <agile 
            :dots="true"
            :navButtons="true"
            :centerMode="true"
            :slidesToShow="1"
            :infinite="true"
            class="imgs"
            >
            
              <img
                v-for="img in selected.images"
                  :key="img.img"
                  :src="img.img"
                  alt=""
                  class="slide"
                  />
              <template slot="prevButton"><a><i class="fa fa-angle-left"></i></a></template>
              <template slot="nextButton"><a><i class="fa fa-angle-right"></i></a></template>
              
            </agile>
          </div>
        </template>

          
        <div class="info">
          <h3>{{ selected.title1 }}</h3>
          <div class="tags">
            <span v-for="tag in selected.tags" :key="tag">{{ tag }}</span>
          </div>
          <div class="tags links">
            <a :href="selected.demo" v-if="selected.demo.length > 1">Demo</a>
            <a :href="selected.source">Source</a>
          </div>
          <div class="description"></div>
        </div>
      </div>
      
      <!--Exit popup when clicking outside border-->
      <div class="background" @click="closeWindow"></div>
    </div>
  </template>
        
        
  <script>
  import Fa from './globals/Fa.vue';
  import { VueAgile } from 'vue-agile';
  export default {
    components: {
      Fa,
      agile: VueAgile
    },
    props: {
      selected: Object,
      closeWindow: Function,
    },
  };
  </script>
  
  
  <style lang="scss">
  @media screen and (max-width: 680px) {
    .popup{
      .content{
        max-width: 80%;
      }
  }
  }
  

  .agile img {
    max-height: 90%;
    object-fit: contain;
  }
 
  .agile {
    //Carousel nav buttons(arrows)
    &__nav-button {
      background: transparent;
      border: none;
      color: rgba(255, 255, 255, 0.350);
      cursor: pointer;
      font-size: 40px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 111%;
      transform: translateX(-96%);
      transition-duration: .3s;
      width: 12%;
      
      &:hover {
        //Change nav button background on hover
        //background-color: rgba(rgba(0, 0, 0, 0.685), .5);
        opacity: 1;
        //Change arrow opacity on hover
        a {
          color: #eeeae6;
        }
      }
      &--prev {
        left: 0
      }
      &--next {
        right: 0
      }
    }
    
    //Page dot position
    &__dots {
      bottom: 0px;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
    }
    
    //Page dot visuals
    &__dot {
      margin: 10px;
        button {
          background-color: transparent;
          border: 1px solid rgb(255, 255, 255, 0.664);
          border-radius: 50%;
          cursor: pointer;
          display: block;
          height: 10px;
          font-size: 0;
          line-height: 0;
          margin: 0;
          padding: 0;
          transition-duration: .3s;
          width: 10px;
      }
    //Current page dot
    &--current,
    &:hover {
      button {
        background-color: rgba(255, 255, 255, 0.500);
      }
    }
    }
  }     
  
  
  //Slide visuals
  .slide {
    display: block;
    // height: 90%;
    // object-fit: 100%
  
  }
  

  //Popup Visuals
  .popup {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1001;
    overflow: hidden;
    top: 0;
    display: flex;
    //Background of Popup
    .background {
      background: #000000e8;
      width: 100%;
      height: 100%;
      position: fixed;
      z-index: -1;
      top: 0;
    }
    .content {
      background: transparent;
      width: 600px;
      margin: auto;
      position: relative;
      bottom: 0;
    }
    .info {
      padding: 0 1.3rem 2rem 1.3rem;
      h3 {
        grid-area: txt;
        font-weight: 500;
        font-size: 1.7rem;
        line-height: 1.4;
        margin-bottom: 0rem;
        margin-top: 0;
      }
      .tags {
        display: flex;
        gap: 0.7rem;
        font-weight: 500;
        font-size: 0.8rem;
        color: var(--txt-med);
      }
      .tags.links {
        a {
          color: white;
          text-decoration: none;
          margin-top: 0.6rem;
        }
      }
    }
    .info > span {
      display: block;
      margin-top: 1rem;
      font-weight: 500;
      font-size: 0.8rem;
      color: var(--txt-med);
    }
    .screenshots {
      padding: 1.3rem;
      img {
        max-height: 400px;
        max-width: 100%;
        margin: auto;
      }
    }
    .close { 
      top: 0px;
      border: 0;
      color: rgba(255, 255, 255, 0.350);
      padding: .4rem;
      box-sizing: border-box;
      position: absolute;
      right: .4rem;
      cursor: pointer;
      z-index: 100;
      background: rgb(0 0 0 / 10%);
      :hover {
        color: white;
        opacity: 1;
    }
  }
  }
  </style>