<template>
    <vue-p5 @sketch="sketch"> </vue-p5>
  </template>
  
  <script>
  import VueP5 from 'vue-p5';
  export default {
    components: {
      VueP5,
    },
    methods: {
      sketch(sk) {
        class Mover {
          constructor(x, y, m) {
            this.pos = sk.createVector(x, y);
            this.vel = sk.constructor.Vector.random2D();
            this.vel.mult(5);
            this.acc = sk.createVector(0, 0);
            this.mass = m;
            this.r = sk.sqrt(this.mass * 2);
          }
          applyForce(force) {
            let f = sk.constructor.Vector.div(force, this.mass);
            this.acc.add(f);
          }
          update() {
            this.vel.add(this.acc);
            this.pos.add(this.vel);
            this.acc.set(0, 0);
          }
          show() {
            //Movers color
            sk.fill('#141824');
            //Movers size
            sk.ellipse(this.pos.x, this.pos.y, this.r * 2);
          }
        }

        //Focus of Movers
        class Attractor {
          constructor(x, y, m) {
            this.pos = sk.createVector(x, y);
            this.mass = m;
            //Static Circle Size
            this.r = sk.sqrt(this.mass) * 2;
          }
          attract(mover) {
            let force = sk.constructor.Vector.sub(this.pos, mover.pos);
            let distanceSq = sk.constrain(force.magSq(), 100, 1000);
            let G = 10;
            let strength = (G * (this.mass * mover.mass)) / distanceSq;
            force.setMag(strength * 0.01);
            mover.applyForce(force);
          }
          show() {
            sk.noStroke();
            //Static Circle Color
            sk.fill('#19233b');
            sk.ellipse(this.pos.x, this.pos.y, this.r * 2);
          }
        }
        let movers = [];
        let attractors = [];
        const width = document.body.clientWidth;
        const height = window.innerHeight;
        sk.setup = () => {
          sk.createCanvas(width, height);
          //number of movers
          for (let i = 0; i < 8; i++) {
            let x = sk.random(width);
            let y = sk.random(height);
            //size range
            let m = sk.random(100, 300, 500);
            movers.push(new Mover(x, y, m));
            //number of attractors
            if (i < 6) {
              //Static Circle Set Random Position
              attractors.push(
                new Attractor(
                  sk.random(width),
                  sk.random(height),
                  //size range
                  sk.random(100, 300, 500)
                )
              );
            }
          }
        };
        
        sk.draw = () => {
          //Background Color
          sk.background('#161c2c');
          for (let mover of movers) {
            mover.update();
            mover.show();
            for (const attractor of attractors) {
              attractor.attract(mover);
            }
          }
          for (const attractor of attractors) {
            attractor.show();
          }
        };
        sk.windowResized = () => {
          sk.resizeCanvas(document.body.clientWidth, window.innerHeight);
        };
        
        //Spawn Moving Circle on mouse click
        sk.mouseClicked = () => {
          movers.push(new Mover(sk.mouseX, sk.mouseY, sk.random(150, 350)));
          if (movers.length > 13) {
            movers.shift();
          }
        };
        sk.keyPressed = (e) => {
          if (e.key !== 'a') return;
          attractors.push(
            new Attractor(sk.mouseX, sk.mouseY, sk.random(150, 350))
          );
        };
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>