        title1: "The University of Houston",
        ort: "Houston, TX",
        txt: "",
      },
      {
        date: "2006  2010",
        extra: "HS Diploma",
        title1: "DeSoto High School",
        ort: "DeSoto, TX",
        txt: "",
      },
    ],
    
    //Project
    projects: [
      {
        id: 0,
        images: [
          {
            img: "/gifs/NoteIt.gif",
          },
          {
            img: "/images/NoteIt.png",
          },
        ],
        title1: "NoteIt Web App",
        tags: ["HTML", "Bootstrap", "Python", "Flask"],
        demo: "",
        source: "https://github.com/big-mond/NoteIt-Web-App",
      },
      {
        id: 1,
        images: [
          {
            img: "/gifs/MovieApp.gif",
          },
          {
            img: "/images/MovieApp.png",
          },
        ],
        title1: "Movie App ReactJS",
        tags: ["HTML", "ReactJS", "CSS"],
        demo: "",
        source: "https://github.com/big-mond/Movie-App-ReactJS",
      },
      {
        id: 2,
        images: [
          {
            img: "/gifs/VideoPlayer.gif",
          },
          {
            img: "/images/VideoPlayer.png",
          },
        ],
        title1: "Web Video Player",
        tags: ["HTML", "JavaScript", "CSS"],
        demo: "",
        source: "https://github.com/big-mond/Web-Video-Player",
      },
      {
        id: 3,
        images: [
          {
            img: "/gifs/MusicPlayer.gif",
          },
          {
            img: "/images/MusicPlayer.png",
          },
        ],
        title1: "Web Music Player",
        tags: ["HTML", "JavaScript", "CSS"],
        demo: "",
        source: "https://github.com/big-mond/Web-Music-Player",
      },
    ],
    //Class given to pull info
    selected: null,
    //Class given to hide sections 
    hide: "hide"
  }),

  
  mounted() {
    //On Scroll enable afterScroll
    window.onscroll = this.lodash.debounce(this.afterScroll, 5)
    this.afterScroll()
    //Connect Sidebar links to page section and scroll on click
    let anchorlinks = document.querySelectorAll('a[href^="#"]')
    for (let item of anchorlinks) {
      item.addEventListener("click", (e) => {
        let hashval = item.getAttribute("href")
        let target = document.querySelector(hashval)
        target.scrollIntoView({
          behavior: "smooth",
          block: "start",
        })
        history.pushState(null, null, hashval)
        e.preventDefault()
      })
    }
    
    

    //Section Scroll Animation
    let observer = new IntersectionObserver((entries) => {
        //Entries are elements to be observed
        entries.forEach((entry) => {
          console.log(entry)
          //Toggle "show" class when observer is
          //intersecting class with "hide"
          entry.target.classList.toggle("show", 
            entry.isIntersecting)
        })
      })
    
    //Finds sections with "hide" class to be observed  
    let hiddenElements = document.querySelectorAll(".hide")
    hiddenElements.forEach((el) => observer.observe(el))

  },
  methods: {
    //Open PopUp Function
    select: function (id) {
      //When selected pulls Project by id
      this.selected = this.projects.find((x) => x.id === id)
      //Hide scrollbar while popup open
      document.body.style.overflow = "hidden"
    },
    closePopUp() {
      //When selected does nothing
      this.selected = null
      //Show scrollbar after closing popup
      document.body.style.overflow = "auto"
    },

    //Sidebar button transition settings after scroll
    afterScroll() {
      //From top for scroll calculation
      let fromTop = document.documentElement.scrollTop
      let navbarHeight = 200
      //Sidebar Links 
      let sidebarLinks = document.querySelectorAll(".sidebuttons a")
      //Sidebar Links Settings
      sidebarLinks.forEach((link) => {
        //Link hash equals link in sidebar
        if (link.hash == "") return
        //Ties section links to link hash
        let section = document.querySelector(link.hash)
        if (section == null) return
        //Settings on Scroll
        if (
          //If top of section is greater than window height
          section.offsetTop <= fromTop + window.innerHeight &&
          //And If top of section and height are greater than navbar height
          section.offsetTop + section.offsetHeight > fromTop + navbarHeight
        ) {
          //Adds "current" class to links after scroll
          link.classList.add("current")
          let currentView = document.querySelectorAll(".current")
          let firstView = document.querySelector(".solid")
          //If not in first view, remove "solid" class
          if  (firstView != null) {
           firstView.classList.remove("solid")
          }
          //then add "solid" class to first link in current view 
         currentView[0].classList.add("solid")
        } else {
          //If in first view, remove "current" class
          link.classList.remove("current")
        }
      })

    },

  },
  props: {
    scrolledDown: Boolean,
  },
};
</script>

